<template>
  <div id="note">
    <head-top title="笔记">
      <router-link slot="right" to="/editNote">
        <i class="iconfont iconadd"></i>
      </router-link>
    </head-top>

    <div class="search-input">
      <van-search placeholder="请输入搜索关键词" v-model="value" @search="onSearch" />
    </div>

    <van-pull-refresh v-model="isLoading" @refresh="getDataList" class="pull_fresh">
      <div class="main" v-if="searchData.length>0">
        <div class="item" v-for="(item, k) in searchData" :key="k" @click="showDetail(item)">
          <div class="item_operate" @click="dataDelete($event,item)">
            <van-icon name="delete" size="1.6rem" color="#f44" />
          </div>
          <div class="title">{{item['SNTtile']||'缺少标题'}}</div>
          <div class="content ql-snow">
            <div v-html="transformValue(item['SNContent'])" class="ql-editor"></div>
          </div>
          <div class="time">{{item['CreateTime'] | formatDate(item['CreateTime']) }}</div>
        </div>
      </div>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import "quill/dist/quill.snow.css";
import { Search, Icon, Dialog } from "vant";
import { ModulePicture } from "@/utils/enum";
export default {
  name: "",
  components: {
    [Search.name]: Search,
    [Icon.name]: Icon,
    headTop
  },
  props: {},
  data() {
    return {
      isLoading: false,
      dataList: [],
      form: {
        courseID: this.$store.getters.courseID,
        studentID: this.$store.getters.studentID,
        pno: 1,
        psize: 10
      },
      funType: ModulePicture.StudentNote,
      value: ""
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.isLoading = true;
      this.dataList = [];
      this.$gatewayHttp.note
        .getList(this.form)
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.sort(r => r["CreateTime"]).reverse();
            res.Datas.forEach(item => {
              this.dataList.push(item);
            });
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
      this.$store.commit("SET_NOTEREFRESH", false);
    },
    showDetail(item) {
      this.$router.push({
        path: "/noteDetail",
        query: {
          title: item["SNTtile"],
          content: item["SNContent"]
        }
      });
    },
    dataDelete(e, item) {
      e.stopPropagation();
      Dialog.confirm({
        message: "确认删除此数据？"
      })
        .then(() => {
          this.$gatewayHttp.note
            .postDelete({ snid: item["SNID"] })
            .then(res => {
              if (res.Result) {
                this.getDataList();
              } else {
                this.$toast("删除失败");
              }
            })
            .catch(err => {
              this.$toast("删除失败");
            });
        })
        .catch(() => {});
    },
    onSearch() {},
    // 图片路径转换
    transformValue(value) {
      let newVal = value.replace(
        /<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
        (match, capture) => {
          //capture,返回每个匹配的字符串
          let wordArray = capture.split("/");
          if (wordArray.length === 3) {
            let filePath = this.$gatewayHttp.resourse.picture(
              wordArray[1],
              wordArray[2]
            );

            return '<img src="' + filePath + '" alt="" />';
          }
          return '<img src="' + capture + '" alt="" />';
        }
      );
      return newVal;
    }
  },
  computed: {
    searchData() {
      if (!this.value) {
        return this.dataList;
      } else {
        return this.dataList.filter(
          r =>
            r["SNTtile"].indexOf(this.value) > -1 ||
            r["SNContent"].indexOf(this.value) > -1
        );
      }
    }
  },
  watch: {
    "$store.getters.noteRefresh"(newVal, oldVal) {
      if (newVal !== oldVal && newVal) {
        this.getDataList();
      }
    }
  }
};
</script>

<style lang='less' scoped>
#note {
  .head-back {
    display: none;
  }
  .iconadd {
    float: right;
    margin-right: 4vw;
    font-size: 1.8rem;
    line-height: 4.3rem;
    color: #fff;
  }
  .search-input {
    .van-search__content {
      background: #ebf0ef;
    }
  }
  .main {
    margin: 1vh auto;
    width: 100vw;
    height: calc(100vh - 4.5rem - 54px - 2vh);
    overflow-y: auto;
    .item {
      position: relative;
      width: 90vw;
      padding: 0 3vw;
      margin: 1vh auto;
      margin-bottom: 1vh;
      text-align: left;
      // border-bottom: 1px solid #fff;
      background-color: #fff;

      word-break: break-all;
      word-wrap: break-word;

      .item_operate {
        position: absolute;
        top: 0.25rem;
        right: 2vw;
        padding: 1rem;
      }

      img {
        width: 160px;
        height: 113px;
      }
      .title {
        padding: 1rem 0;
        margin-right: 3rem;
        font-size: 1.5rem;
        line-height: 1.2;
        position: relative;
        color: #555;
        &::after {
          content: " ";
          position: absolute;
          bottom: 0;
          left: 0;
          height: 1px;
          width: 100%;
          transform: scaleY(0.1);
          background-color: #000;
        }
      }
      .content {
        margin: 0 -2vw;
        max-height: 20vh;
        overflow-y: auto;
        // font-size: 1.25rem;
        // line-height: 1.5;
        // color: rgba(102, 102, 102, 1);
        // background: #ebf0ef;
        // border-radius: 0.5rem;

        pre {
          white-space: pre-wrap;
          white-space: -moz-pre-wrap;
          white-space: -pre-wrap;
          white-space: -o-pre-wrap;
          margin: 0.5rem auto;
          font-size: 1.25rem;
        }
      }
      .time {
        padding-top: 0.3rem;
        padding-bottom: 1rem;
        font-size: 1.2rem;
        line-height: 1.5;
        color: rgba(102, 102, 102, 1);
      }
    }
  }

  .pull_fresh {
    /deep/ .van-pull-refresh__track {
      height: calc(100vh - 4.5rem - 54px - 2vh);
    }
  }
}
</style>


